.soc-title { color: #464646; font-size: 0.875rem; font-weight: 300; margin-bottom: 15px; line-height: 24px; }

.catalog-menu-outer { padding-top: 25px; }

.catalog-menu__list li { margin-bottom: 15px; }
.catalog-menu__list a { color: #333; font-size: 0.875rem; font-weight: 400; }
.catalog-menu__list a:hover { text-decoration: underline; }

.catalog-menu__top { padding: 40px 0; border-bottom: 1px solid #d5d5d5; }

.reccomends-container { display: flex; justify-content: space-between; }

.reccomends-picture img { margin-bottom: 17px; box-shadow: 0 0.313rem 1.375rem rgba(26, 35, 42, 0.45); }

.reccomends-text .name { font-size: 0.875rem; font-weight: 600; color: #333; line-height: 20px; margin-bottom: 13px; }
.reccomends-text .price { color: #8b8b8b; line-height: 24px; font-size: 1.25rem; }

.reccomends__item { max-width: 130px; }

.reccomends { padding: 40px 0; }
.reccomends .reccomends__title { padding-bottom: 35px; }

.slick-arrow { transition: all 0.3s; }
.slick-arrow:hover { opacity: 0.5; }

.slick-dots li.slick-active button { background: #515151 !important; }

.slick-dots button { background: #c1c2c2 !important; }

.slick-dots li { padding: 0 6px; }

.heaader--second .header-top { padding: 8px 0; background-color: rgba(22, 23, 24, 0.76); }

.heaader--second { background-image: none !important; min-height: initial !important; }

.sign__user-image { display: inline-block; vertical-align: middle; width: 40px; height: 40px; border-radius: 50%; }

.sign__user-name { display: inline-block; vertical-align: middle; margin-left: 20px; }

.counter-val { margin: 0 9px; }

.counter-val, .counter-nav { display: inline-block; padding: 5px; vertical-align: middle; color: #b8b8b8; font-size: 1rem; font-weight: 400; }
.counter-val span, .counter-nav span { font-size: 23px; color: #b8b8b8; left: 4px; top: 2px; position: absolute; line-height: 10px; }

.next span { left: 1px; top: 3px; }

.counter-nav { text-decoration: none; cursor: pointer; width: 1.125rem; position: relative; height: 1.125rem; border: 0.063rem solid #d6d6d6; background-color: transparent; border-radius: 50%; }

.counter { margin-bottom: 16px; }

.order-section .container:before { content: ""; position: absolute; right: 50%; width: 1px; background-color: #d5d5d5; top: 0; bottom: 0; }

.order { position: relative; border-bottom: 1px solid #d5d5d5; padding-bottom: 38px; -webkit-box-sizing: content-box; -moz-box-sizing: content-box; box-sizing: content-box; margin-right: 20px; }
.order:last-child { border-bottom: 0; }
.order .h3 { padding-bottom: 8px !important; max-width: 160px; }

.order-item__author { padding-bottom: 6px !important; }

.order-section__inside, .order-get { padding-top: 25px; }

.order-section__inside .h3 { padding-bottom: 40px; }

.order-get { padding-left: 20px; }
.order-get .h3 { padding-bottom: 34px; }

.order-section { padding-top: 80px; box-shadow: inset 0.094rem 0.163rem 0.625rem rgba(0, 0, 0, 0.05); }

.order-section .container { padding-bottom: 40px; }

.delete-order { width: 40px; height: 40px; position: absolute; background: transparent; border: 1px solid #d5d5d5; border-radius: 50%; right: 0; top: 0; font-size: 0; }
.delete-order:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.delete-order .icon-close { font-size: 15px; color: #d5d5d5; display: inline-block; vertical-align: middle; }

.order-form__indide { border-bottom: 1px solid #d5d5d5; padding-bottom: 50px; margin-bottom: 30px; }

.grey-drop { width: 320px; background: #f2f2f2 !important; border-color: #f2f2f2 !important; height: 40px; margin-bottom: 30px; line-height: 16px; position: relative !important; z-index: auto; }
.grey-drop .text { font-size: 14px; line-height: 15px; overflow: hidden; width: 90%; white-space: nowrap; text-overflow: ellipsis; }

.cook_write { border: 0.063rem solid #c4c4c4; border-radius: 1.25rem; height: 40px; width: 320px; margin-bottom: 30px; font-size: 14px; padding-left: 20px; }

.input-title { font-size: 14px; margin-bottom: 10px; }

.add_comm, .show_fild { cursor: pointer; font-size: 14px; display: inline-block; margin-bottom: 21px; border-bottom: 1px dotted #333; }
.add_comm:hover, .show_fild:hover { border-bottom-color: transparent; }

.order-get__auth { font-size: 0.875rem; line-height: 24px; color: #2d2e2f; font-weight: 400; margin-bottom: 34px; }

.auth-bask { color: #919191; font-style: italic; text-decoration: underline; }

.sogl { text-decoration: underline; color: #6c6969; }

.sum_price { color: #333; font-family: "Open Sans"; line-height: 30px; font-size: 0.875rem; font-weight: 700; }
.sum_price span { font-size: 1.5rem; font-weight: 400; }

.sum_price { margin-bottom: 30px; }

input[type="checkbox"] { position: relative; -webkit-appearance: none; height: 1.25em; width: 1.25em; margin-bottom: -0.25em; margin-right: 5px; -webkit-transition: border-color 0.2s; -moz-transition: border-color 0.2s; -o-transition: border-color 0.2s; transition: border-color 0.2s; padding: 0; border: 1px solid #c4c4c4; -webkit-border-radius: 2px; -moz-border-radius: 2px; border-radius: 2px; background-color: #fff; }

input[type="checkbox"]:checked:before { content: "✓"; position: absolute; font-weight: bold; font-size: 0.85em; left: 1px; top: 3px; text-align: center; width: 1.25em; color: #27be6e; line-height: 11px; }


